body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

:root {
  --body-bg-color: #e2dcff;
  --base-font-size: 18px;
  --base-medium-font-size: 14px;
  --base-small-font-size: 10px;
  --header-font-family: adelle, serif;
  --paragraph-font-family: proxima-nova, serif;
  --header-line-height: 1.1;
  --header-margin-bottom: 1rem;
  --header-color: #25262b;
  --h1-font-size: 4.236rem;
  --h2-font-size: 2.618rem;
  --h2-font-weight: 400;
  --h3-font-size: 1.618rem;
  --h3-font-weight: 400;
  --h3-font-family: var(--paragraph-font-family);
  --paragraph-font-size: 1rem;
  --paragraph-font-weight: 400;
  --input-height: 3rem;
  --input-border-radius: 0.5rem;
  --input-width: 80%;
  --input-bg-color: #6046ff;
  --input-box-shadow: 0 2rem 4rem 0 #8677ff;
  --input-font-size: var(--h3-font-size);
  --input-color: #ffffff;
  --input-border: none; }

body {
  background-color: #e2dcff;
  background-color: var(--body-bg-color); }

html {
  font-size: 18px;
  font-size: var(--base-font-size); }

h1,
h2,
h3 {
  font-family: adelle, serif;
  font-family: var(--header-font-family);
  line-height: 1.1;
  line-height: var(--header-line-height);
  margin: 1rem 0;
  margin: var(--header-margin-bottom) 0;
  color: #25262b;
  color: var(--header-color); }

h1 {
  font-size: 4.236rem;
  font-size: var(--h1-font-size); }

h2 {
  font-size: 2.618rem;
  font-size: var(--h2-font-size); }

h3 {
  font-size: 1.618rem;
  font-size: var(--h3-font-size);
  font-family: proxima-nova, serif;
  font-family: var(--h3-font-family); }

@media (max-width: 60em) {
  html {
    font-size: 14px;
    font-size: var(--base-medium-font-size); } }

@media (max-width: 30em) {
  html {
    font-size: 10px;
    font-size: var(--base-small-font-size); } }

.container {
  display: grid;
  text-align: center;
  grid-template-rows: 3rem 1fr 3rem;
  grid-template-columns: 0.5fr 1fr 0.5fr;
  height: 100vh; }

@media (max-width: 60em) {
  .container {
    grid-template-columns: 0.5fr 3fr 0.5fr; } }

.header {
  grid-column: 1 / -1; }

.main {
  grid-column: 2 / -2;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr;
  grid-template-rows: min-content 1fr; }

.footer {
  grid-column: 1 / -1; }

input {
  height: 3rem;
  height: var(--input-height);
  width: 80%;
  width: var(--input-width);
  color: #ffffff;
  color: var(--input-color);
  background-color: #6046ff;
  background-color: var(--input-bg-color);
  box-shadow: 0 2rem 4rem 0 #8677ff;
  box-shadow: var(--input-box-shadow);
  font-size: 1.618rem;
  font-size: var(--input-font-size);
  font-family: proxima-nova, serif;
  font-family: var(--paragraph-font-family);
  text-align: center;
  border-radius: 0.5rem;
  border-radius: var(--input-border-radius);
  outline: none;
  border: none;
  border: var(--input-border);
  margin-bottom: 1rem; }

.date-display {
  margin-top: 4rem; }

